.nav{
	position: fixed;
	top: 0;
	width: 100%;
	padding: 20px;
	height: 30px;
	z-index: 1;
	display: flex;
	justify-content: space-between;

	/* Animations */
    transition-timing-function: ease-in;
    transition: all 0.5s;
}
.nav--black{
	background-color: #111;
}
.nav--logo{
	position: fixed;
	left: 20px;
	width: 80px;
	object-fit: contain;
}
.nav--usercontent{
	position: fixed;
	right: 20px;
	display: flex;
}
.nav--avatar{
	width: 30px;
	object-fit: contain;
}
.nav--username{
	color: white;
	padding-right: 7px;
	align-self: center;
}